import logo from './logo.svg';
import './App.css';
import { BrowserRouter as Router, Link, Route, Switch } from 'react-router-dom';
import OnionConnoisseur from './components/onion-connisseur/OnionConnoisseur';

import React, { useEffect } from 'react';
import { Box, Text } from '@chakra-ui/layout';
import Home from './components/home/Home';

import { useMediaQuery } from 'react-responsive'
import MobileNavbar from './components/navbar/MobileNavbar';
import LargerNavbar from './components/navbar/LargerNavbar';



function App() {
  const mobileNav = useMediaQuery({query: '(min-device-width: 48em)'})
  return (
    <Router>
      {
      mobileNav ? 
      <LargerNavbar w='75%' />
      :
      <MobileNavbar/>
      }
      <Box w='100%' pt='5vh' />
        <Switch>
          <Route exact path='/' component={Home} />
          <Route exact path='/onion_connoisseur' component={OnionConnoisseur} />
        </Switch>
      </Router>
  )
}

export default App;
